<template>
  <div class="addInfo">
    <div class="top">
      <div class="cancel" @click="hideDialog"></div>
    </div>
    <div class="title">发布公告</div>
    <div class="form">
      <div class="form-item">
        <label for="title">公告标题</label>
        <input
          type="text"
          placeholder="请输入标题"
          id="title"
          class="form-item-content"
          v-model="monitoredMixedObj.title"
          ref="name"
        />
      </div>
      <div class="form-item content">
        <label for="content">公告内容</label>
        <textarea
          id="content"
          class="form-item-content"
          v-model="monitoredMixedObj.content"
          ref="content"
        ></textarea>
      </div>
      <div class="form-item reason">
        <label for="reason">创建原因</label>
        <input
          type="text"
          placeholder=""
          id="reason"
          class="form-item-content"
          v-model="monitoredMixedObj.reason"
          ref="reason"
        />
      </div>

      <div class="form-item">
        <label for="platform">发布平台</label>
        <select v-model="monitoredMixedObj.platform" id="platform">
          <option value="10000">全平台</option>
          <option value="10001">IOS</option>
          <option value="10002">微信小程序</option>
        </select>
      </div>

      <div class="form-item">
        <label for="status">公告发布</label>
        <select v-model="monitoredMixedObj.status" id="status">
          <option value="11001">普通</option>
          <option value="11000">紧急</option>
        </select>
      </div>
    </div>
    <div class="buttonZone">
      <button class="cancelBtn other-button" @click="hideDialog">取消</button>
      <button class="ensureBtn submit-button" @click="checkInput">确认</button>
    </div>

    <div class="edit-reason" v-show="ifShowEditReason">
      <edit-reason-dialog
        @cancelClick="hideEditReason"
        @ensureClick="submit"
      ></edit-reason-dialog>
    </div>
  </div>
</template>

<script>
import EditReasonDialog from "@/pages/internal/components/content/adminTools/editReasonDialog/EditReasonDialog";
import { $submitUserInfo } from "@/pages/internal/network/adminTools/info";

export default {
  name: "e-AddInfo",
  data() {
    return {
      monitoredMixedObj: {
        platform: "10000",
        title: "",
        content: "",
        reason: "",
        status: "11001",
      },
      ifShowEditReason: false,
    };
  },
  methods: {
    hideDialog() {
      this.$emit("hideDialog");
    },
    checkInput() {
      if (this.monitoredMixedObj.title === "") {
        this.$toast.error("请填写标题", 240);
        return;
      }
      if (this.monitoredMixedObj.content === "") {
        this.$toast.error("请填写公告内容", 240);
        return;
      }
      if (this.monitoredMixedObj.reason === "") {
        this.$toast.error("请填写创建理由", 240);
        return;
      }
      this.showEditReason();
      console.log(this.ifShowEditReason);
    },
    showEditReason() {
      this.ifShowEditReason = true;
    },
    hideEditReason() {
      this.ifShowEditReason = false;
    },
    async submit(edit_reason) {
      this.hideEditReason();
      let data = {
        user: {
          id: window.localStorage.getItem("userID"),
        },
        version: "1.0.0",
        create_reason: edit_reason,
        work_line_id: 1600,
        work_operation: 4200,
        content: {
          // "send_id": 6,
          send_id: 6,
          send_id: Number(window.localStorage.getItem("userID")),
          platform: this.monitoredMixedObj.platform,
          create_reason: this.monitoredMixedObj.reason,
          title: this.monitoredMixedObj.title,
          status: this.monitoredMixedObj.status, // URGENT = 11000，ACTIVE = 11001, INACTIVE = 11002 【创建使用11001】
          content: {
            text: this.monitoredMixedObj.content,
          },
        },
      };
      console.log(data);
      try {
        const response = await $submitUserInfo(data);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          this.$toast.success("提交成功", 240);
        } else {
          this.$toast.error("提交失败", 240);
        }
      } catch (e) {
        this.$toast.error("提交失败", 240);
      }
    },
  },
  watch: {},
  components: {
    EditReasonDialog,
  },
};
</script>

